#pardot-form {
  --form-checkbox--font-size: #{$size-x-large};
  --form-checkbox-icon--font-size: #{$size-tiny};
  --form-checkbox-icon-background-color: #{$color-light-purple};
  --form-checkbox-icon-position: 0.5rem;

  input[type="checkbox"],
  input[type="radio"] {
    @include form-choice-icon;
    opacity: 0;
    //accessiblity issue
    position: relative;
    height: 1px;
    width: 1px;
    margin-top: -1px;
    margin-left: 0;
    display: block;
  }

  .form-group--choice {
    label:first-child {
      position: static;
      margin-bottom: $margin-default*0.5;
      padding-left: 0;
      color: var(--form-field-input-text-color, $color-black);
    }

    .choice-wrapper {
      position: relative;
      margin-right: $margin-default*0.75;
      margin-bottom: $margin-default*0.5;
      padding-left: $padding-default;
      color: var(--form-field-input-text-color, $color-black);

      &:last-of-type {
        margin-right: 0;
      }
    }
  }
  .checkbox--opt-in {
    input[type="checkbox"] {
      flex-shrink: 0;
      width: var(--form-checkbox--font-size);
      height: var(--form-checkbox--font-size);
      z-index: 1;
    }
    .opt-in-label {
      padding-left: $padding-default*0.375;
      color: var(--form-field-input-text-color, $color-black);
    }
  }
}